import React, { useEffect, useState } from 'react'
import CoursesBlock from '../../../components/courses_block'
import SearchIcon from '@mui/icons-material/Search';
import Chip from '@mui/material/Chip';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';

import "./style.css"
import { Fade } from '@mui/material';

function CoursesPage(props) {
    let user = props.user
    let courses = user && props.user.courses
    let windowInfo = props.windowInfo
    const [firstLoad, setFirstLoad] = useState(true)
    const [selectedCategories, setSelectedCategories] = useState([])

    useEffect(() => {
        if(firstLoad && courses) {
            courses.loadCoursesPerCategory(10, "Nuovi")
            courses.loadCoursesPerCategory(10, "Per iniziare")
            courses.loadCoursesPerCategory(10, "I pi seguiti")
            courses.loadCategories()
            setFirstLoad(false)
        }
    }, [courses])

    function manageCategories(category) {
        let _temp = [...selectedCategories]
        if(selectedCategories.includes(category)) {
            let index = _temp.indexOf(category)
            if(index > -1) _temp.splice(index, 1)
        } else {
            _temp.push(category)
        }
        setSelectedCategories(_temp)
    }

    return (
        <Fade in={true}>
        <div id="course_page">
            <div className="search_bar">
                <input placeholder="Cerca..."/><SearchIcon className="orange_icon"/><br/>
                {/* <InputLabel id="demo-simple-select-standard-label">Age</InputLabel>
                <Select
                    labelId="demo-simple-select-standard-label"
                    id="demo-simple-select-standard"
                    multiple
                    value={selectedCategories}
                    onChange={handleChange}
                    label="Age"
                >
                    {
                    courses && Object.keys(courses.getCategories()).map((category_id) => {
                        return <MenuItem
                                label={courses.getCategories()[category_id]}
                                onClick={() => manageCategories(category_id)}></MenuItem>
                    })
                    }
                    <MenuItem value="">
                    <em>None</em>
                    </MenuItem>
                    <MenuItem value={10}>Ten</MenuItem>
                    <MenuItem value={20}>Twenty</MenuItem>
                    <MenuItem value={30}>Thirty</MenuItem>
                </Select> */}
            </div>
            <CoursesBlock
            id={'one'}
            content={courses && courses.getCoursesPerCategory()['Nuovi']}
            title={"Nuovi"}
            layout="long_right"
            windowInfo={windowInfo}
            />
            <CoursesBlock
            id={'two'}
            content={courses && courses.getCoursesPerCategory()['Per iniziare']}
            title={"Per iniziare"}
            layout="squared_left"
            windowInfo={windowInfo}
            />
            <CoursesBlock
            id={'two'}
            content={courses && courses.getCoursesPerCategory()['I pi seguiti']}
            title={"I pi seguiti"}
            layout="squared_left"
            windowInfo={windowInfo}
            />
        </div>
        </Fade>
    )
}

export default CoursesPage
