import React, { useState, useEffect } from 'react'

import {AtomicBlockUtils, convertToRaw, EditorState, Entity, RichUtils, ContentState, CompositeDecorator, convertFromRaw} from 'draft-js';
import 'draft-js/dist/Draft.css';
import '@draft-js-plugins/image/lib/plugin.css'
import '@draft-js-plugins/image/lib/plugin.css';
import draftToMarkdown from 'draftjs-to-markdown'
import Editor from '@draft-js-plugins/editor';
import createImagePlugin from '@draft-js-plugins/image';
import {markdownToDraft} from "markdown-draft-js"
import {marked} from 'marked'

import "./style.css"

function MarkupEditor(props) {
    let textContent = props.content
    let setTextContent = props.setContent
    let blockLayout = props.block != undefined ? props.block : true
    const imagePlugin = createImagePlugin();
    const decorator = new CompositeDecorator([
        {
            strategy: findImageEntities,
            component: Image,
        },
    ])
    const plugins = [imagePlugin];
    const [content, setContent] = useState(() => EditorState.createEmpty())
    const customStyle = {
        RED : {color: 'red'}
    }

    function handleKeyCommand(command, editorState) {
        const newState = RichUtils.handleKeyCommand(editorState, command)

        if(newState) {
            setContent(newState)
            return 'handled'
        } else return 'not-handled'
    }

    function styleMode(e, type) {
        e.preventDefault()
        setContent(RichUtils.toggleInlineStyle(content, type))
    }

    function toggleBlockType(e, type) {
        e.preventDefault()
        setContent(RichUtils.toggleBlockType(content, type))

    }

    function toMarkDown() {
        let rawContent = convertToRaw(content.getCurrentContent())

        let markdown = draftToMarkdown(rawContent)
        console.log(toDraft(markdown))
        return markdown
    }

    function toDraft(markdown) {
        let rawObject = markdownToDraft(markdown, {
            blockEntities: function (item) {
                return {
                    type: "atomic",
                    mutability: "IMMUTABLE",
                    data: {
                        src: item.src
                    }
                }
            }
        })
        let contentState = convertFromRaw(rawObject)
        let newEditorState = EditorState.createWithContent(contentState);
        // setContent(newEditorState)
        console.log(contentState)
    }

    function findImageEntities(contentBlock, callback, contentState) {
        contentBlock.findEntityRanges(
            (character) => {
            const entityKey = character.getEntity();
            return (
                entityKey !== null &&
                contentState.getEntity(entityKey).getType() === 'IMAGE'
            );
            },
            callback
        );
    }

    function insertImage(e) {
        if(e !== undefined) {
            let input = e.target
            let reader = new FileReader();
            reader.onload = function(){
            let dataURL = reader.result;
            let currentContent = content.getCurrentContent()
            let currentStateWithEntity = currentContent.createEntity(
                "IMAGE",
                "IMMUTABLE",
                {src:dataURL}
            )
            let entityKey = currentStateWithEntity.getLastCreatedEntityKey();
            let newEditorState = EditorState.set(content, {
                currentContent: currentStateWithEntity
            })
            newEditorState = AtomicBlockUtils.insertAtomicBlock(newEditorState, entityKey, " ")
            setContent(newEditorState)
            };
            reader.readAsDataURL(input.files[0]);
        }
    }

    function updateContent() {
        let markdown = toMarkDown()
        if(setTextContent != undefined)
            setTextContent(markdown)
    }

    function prova() {
        let markdown = toMarkDown()
        toDraft(markdown)
    }

    useEffect(() => {
        if(textContent != "") {
            toDraft(textContent)
        }
    }, [])

    return (
        <div className={"text_editor_container " + (blockLayout ? "block" : "")}>
            <div className="style_container">
                <div>
                    <button
                    onMouseDown={(e) => toggleBlockType(e, 'header-one')}
                    className="button bounce"><b>T1</b></button>
                    <button
                    onMouseDown={(e) => toggleBlockType(e, 'unordered-list-item')}
                    className="button bounce"><b>OL</b></button>
                    <button
                    onMouseDown={(e) => toggleBlockType(e, 'ordered-list-item')}
                    className="button bounce"><b>UL</b></button>
                    <button
                    onMouseDown={(e) => styleMode(e, 'BOLD')}
                    className="button bounce"><b>B</b></button>
                    <button
                    onMouseDown={(e) => styleMode(e, 'ITALIC')}
                    className="button bounce"><i>I</i></button>
                    <button
                    onMouseDown={(e) => styleMode(e, 'UNDERLINE')}
                    className="button bounce"><i>U</i></button>
                    <button
                    onMouseDown={(e) => insertImage()}
                    className="button bounce">
                        <label htmlFor="insert_image">Immagine</label>
                        <input
                        id="insert_image"
                        type="file"
                        accept="image/*"
                        style={{display: 'none'}}
                        onChange={(e) => insertImage(e)}/>
                    </button>
                    <button
                    onMouseDown={() => prova()}
                    className="button bounce"><i>S</i></button>

                </div>
                <div>
                    <button
                    onMouseDown={() => setContent(EditorState.undo(content))}
                    className="button bounce">U</button>
                    <button
                    onMouseDown={() => setContent(EditorState.redo(content))}
                    className="button bounce">D</button>
                </div>
            </div>
            <hr className="m-0 mb-3"/>
            <div className="text_editor">
                <Editor
                plugins={plugins}
                customStyleMap={customStyle}
                editorState={content}
                onChange={setContent}
                handleKeyCommand={handleKeyCommand}
                placeholder="Scrivi..."

                //onChange={updateContent}
                />
            </div>
        </div>
    )
}

const Image = (props) => {
    const {
      height,
      src,
      width,
    } = props.contentState.getEntity(props.entityKey).getData();

    return (
    <img src={src} />
    );
};

export default MarkupEditor
