import React, { useState, useEffect } from 'react';
import SearchIcon from '@mui/icons-material/Search';
import News from '../../../../components/news_card';
import { Col, Row } from "react-bootstrap"

function NewsHome(props) {
    let content = props.content
    let windowInfo = props.windowInfo
    let ref = props.ref

    useEffect(() => {
        content.loadGeneralNews(6)
    }, []);

    return <div id="news-home" ref={ref}>
        <div className="centered">
            <h1 className="text-shadow text-center">News</h1>
            <div className="search_bar text-center">
                <input placeholder="Cerca..."/><SearchIcon className="orange_icon"/><br/>
            </div>
            <div className="text-center"> 
                <button className="button">Del giorno</button>
                <button className="button">Per categoria</button>
                <button className="button">Per i tuoi gusti</button>
            </div>
        </div>
        <div className="small_news_container">
            <Row>
                <Col md="1"></Col>
                <Col md="4">
                    <News
                    content={Object.values(content.getGeneralNews())[0]}
                    windowInfo={windowInfo}
                    layout="squared"
                    selected={true}/>
                    <News
                    content={Object.values(content.getGeneralNews())[1]}
                    windowInfo={windowInfo}
                    layout="squared"
                    selected={true}/>
                </Col>
                <Col md="2">
                    <News
                    content={Object.values(content.getGeneralNews())[2]}
                    windowInfo={windowInfo}
                    layout="squared"
                    selected={true}/>
                    <News
                    content={Object.values(content.getGeneralNews())[3]}
                    windowInfo={windowInfo}
                    layout="squared"
                    selected={true}/>
                </Col>
                <Col md="4">
                    <News
                    content={Object.values(content.getGeneralNews())[4]}
                    windowInfo={windowInfo}
                    layout="squared"
                    selected={true}/>
                    <News
                    content={Object.values(content.getGeneralNews())[5]}
                    windowInfo={windowInfo}
                    layout="squared"
                    selected={true}/>
                </Col>
                <Col md="1"></Col>
            </Row>
        </div>

    </div>;
  
}

export default NewsHome;
