class User{
    constructor() {
        this.name = ""
        this.surname = ""
        this.birthday = ""
        this.email = ""
        this.username = ""
        this.password = ""
        this.codice_invito = ""
        this.avatar = null
        this.xp = ""
    }

    load({ name, surname, birthday, email, username, password, codice_invito, avatar = null, xp }) {
        this.name = name
        this.surname = surname
        this.birthday = birthday
        this.email = email
        this.username = username
        this.password = password
        this.codice_invito = codice_invito
        this.avatar = avatar
        this.xp = xp
    }

    setName(name) {this.name = name}
    setSurname(surname) {this.surname = surname}
    setBirthday(birthday) {this.birthday = birthday}
    setEmail(email) {this.email = email}
    setUsername(username) {this.username = username}
    setPassword(password) {this.password = password}
    setCodiceInvito(codice_invito) {this.codice_invito = codice_invito}
    setAvatar(avatar) {this.avatar = avatar}
    setXP(xp) {this.xp = xp}

    getName() {return this.name}
    getSurname() {return this.surname}
    getBirthday() {return this.birthday}
    getEmail() {return this.email}
    getUsername() {return this.username}
    getPassword() {return this.password}
    getCodiceInvito() {return this.codice_invito}
    getAvatar() {return this.avatar}
    getXP() {return this.xp}
}

export default User