import ModuleController from "./module_controller";
import QuestionController from "./question_controller"
import wallpaper from "../media/img/draghi.jpeg"

class CreateModuleController {
    constructor(module = new ModuleController(), args = new QuestionController(), state = undefined) {
        this.module = module
        this.module.setOverrideState((() => this.updateInfo()).bind(this))
        this.args = args
        this.state = state
    }

    // TEST
    loadTEST() {
        // TEST
        this.load({
            module : {
                author: "Pino Pascoli",
                socialPage: "societ",
                wallpaper: wallpaper,
                modules: {
                    1: {
                        title: "Primo modulo",
                        pages: {
                            1 : {
                                type: 'page',
                                content: "Contenuto 1"
                            },
                            2 : {
                                type: 'page',
                                content: "Contenuto 1"
                            },
                            3 : {
                                type: 'page',
                                content: "Contenuto 1"
                            },
                        }
                    },
                    2: {
                        title: "Secondo modulo",
                        pages: {
                            1 : {
                                type: 'page',
                                content: "Contenuto 1"
                            },
                            2 : {
                                type: 'page',
                                content: "Contenuto 1"
                            },
                            3 : {
                                type: 'page',
                                content: "Contenuto 1"
                            },
                            4 : {
                                type: 'page',
                                content: "Contenuto 1"
                            },
                        }
                    },
                }
            },
        })
    }

    setState(state) {
        this.state = state
        this.updateInfo()
    }

    updateInfo() {
        if(this.state !== undefined) {
            this.state(new CreateModuleController(this.module, this.args, this.state))
        }
    }

    load({module, args}) {
        if(module != undefined)
            this.module.load(module)
        if(args != undefined)
            this.setArguments(args)
        this.updateInfo()
    }

    loadArguments() {
        // DA IMPLEMENTARE
        // TEST
        let _args = {
            1 : "ARGOMENTO 1",
            2 : "ARGOMENTO 2",
            3 : "ARGOMENTO 3"
        }

        let args = new QuestionController()
        args.setOverrideUpdateInfo((() => this.updateInfo()).bind(this))
        args.load({
            id: null,
            title: "",
            choices: _args,
            acceptedChoices: null
        })
        this.setArguments(args)
    }

    getModule() { return this.module }
    getArguments() {return this.args}

    setModule(module, _auto_save) {
        this.module = module
        if(_auto_save) this.updateInfo()
    }

    setArguments(args, _auto_save) {
        this.args = args
        if(_auto_save) this.updateInfo()
    }

    deletePage(moduleId, pageId) {
        let module = this.module.getModules()[moduleId]
        let pages = module['pages']
        if(pages !== undefined) {
            delete pages[pageId]
            module['pages'] = pages
            let newModules = this.module.getModules()
            newModules[moduleId] = module
            this.module.setModules(newModules)
        }
        
        this.updateInfo()
    }

    deleteModule(moduleId) {
        let _modules = this.module.getModules()
        delete _modules[moduleId]
        this.module.setModules(_modules)
    }

}

export default CreateModuleController